import React from 'react';
import { Box, Typography } from '@mui/material';

import PlayArrowIcon from '@mui/icons-material/PlayArrow';

import backgroundImage from '../assets/image/background.png';
import logoImage from '../assets/image/logo.png';

import StyledButton from '../components/StyledButton';
import { useNavigate } from 'react-router-dom';
import VolumeButton from '../components/VolumeButton';
import SocialLinks from '../components/SocialLinks';


function Home() {
  const navigate = useNavigate();

  const goToPlay = () => {
    // Navigate to a different route with state and replace
    navigate('/play', { replace: true, state: { referrer: 'dashboard' } });
  };

  const toggleMute = () => { }

  return (
    <Box
      sx={{
        width: '100%',
        height: '100vh',
        backgroundImage: `url(${backgroundImage})`,
        backgroundSize: '100% 100%',
        display: 'flex',
        flexDirection: 'column',
        alignItems: 'center',
        color: 'white',
        position: 'relative'
      }}
    >
      <VolumeButton onToggleMute={toggleMute} />
      <Box
        component={'img'}
        src={logoImage}
        py={10}
        sx={{ width: { sm: '250px', xs: '200px' } }}
      />

      <Typography variant="h6" sx={{ fontFamily: "'Press Start 2P' !important", fontSize: { md: 30, xs: 20 }, mb: 1, textAlign: 'center' }}>
        Airdrop 01 for Holders
      </Typography>
      <Typography variant="h6" sx={{ fontFamily: "'Press Start 2P' !important", fontSize: { md: 30, xs: 20 }, mb: 3, textAlign: 'center' }}>
        Multiply your $TERRY Bags
      </Typography>

      <StyledButton
        variant="contained"
        endIcon={<PlayArrowIcon />}
        sx={{ mt: 5 }}
        onClick={goToPlay}
      >
        PLAY
      </StyledButton>

      <SocialLinks position={'absolute'} />
    </Box>
  );
}

export default Home;