import React, { useRef, useState, useEffect } from 'react';
import { Box, Grid, TextField, Typography } from '@mui/material';

import backgroundImage from '../assets/image/background.png';
import logoImage from '../assets/image/logo.png';
import VolumeButton from '../components/VolumeButton';
import StyledButton from '../components/StyledButton';

import sound from '../assets/play sound.ogg';

import hhImage from '../assets/image/hh.gif';
import htImage from '../assets/image/ht.gif';
import ttImage from '../assets/image/tt.gif';
import fixedImage from '../assets/image/fixed.png';

import { useNavigate } from 'react-router-dom';
import { toast } from 'react-toastify';
import axios from 'axios';
import SocialLinks from '../components/SocialLinks';


const buttonList = [
  { id: 'hh', label: 'HEAD+HEAD' },
  { id: 'ht', label: 'HEAD+TAIL' },
  { id: 'tt', label: 'TAIL+TAIL' },
];

function Play() {
  const navigage = useNavigate()

  const [userhash, setUserhash] = useState('')

  const [activeId, setActiveId] = useState('');
  const [result, setResult] = useState('');
  const [playingGif, setPlayingGif] = useState(false)
  const [imageSource, setImageSource] = useState(fixedImage);
  const [imageFSource, setImageFSource] = useState(fixedImage);
  const [isEndStreak, setIsEndStreak] = useState(false);
  const [isLost, setIsLost] = useState(false);
  const [isTossing, setIsTossing] = useState(false);
  const [streakCount, setStreakCount] = useState(0);
  const [attemptsCount, setAttpemtsCount] = useState(0);
  const [statusChanged, setStatusChanged] = useState(false);
  const [walletAddress, setWalletAddress] = useState('')
  const [displayAddress, setDisplayAddress] = useState('');


  const audioRef = useRef(null);

  const handleSetActiveId = (id) => {
    setActiveId(id);
  };

  const gotoHome = () => {
    navigage('/')
  }

  const playGif = () => {
    setPlayingGif(true);
    setTimeout(() => {
      setPlayingGif(false);
    }, 10);
  }

  const tossCoins = async () => {
    if (!activeId) {
      toast.info("Please select one")
      return;
    }
    if (isTossing) {
      return;
    }
    setIsTossing(true);
    try {
      const gameData = await axios.post(`https://chronicles.casino/api/coinflip/play`, { userhash, id: activeId });
      const tossResult = gameData.data.result;
      // Set the video source based on the toss result
      if (tossResult === 'hh') {
        setImageSource(hhImage);
      } else if (tossResult === 'ht') {
        setImageSource(htImage);
      } else {
        setImageSource(ttImage);
      }
      playGif()

      setResult(tossResult);
      setStatusChanged(!statusChanged)
      setAttpemtsCount(attemptsCount + 1)


      setTimeout(() => {
        if (activeId === tossResult) {
          setStreakCount(streakCount + 1);
        } else setStreakCount(0)
        setIsTossing(false)
      }, 1750);

    } catch (error) {
      console.log('error', error)
      setIsTossing(false)
    }

  };

  const formatAddress = (address) => {
    if (!address) return ''; // Handle empty or undefined addresses
    const start = address.slice(0, 7); // Get the first 5 characters
    const end = address.slice(-7); // Get the last 5 characters
    return `${start}....${end}`; // Concatenate with ellipses
  }


  const handleAddressChange = (event) => {
    const fullAddress = event.target.value;
    setWalletAddress(fullAddress); // Store the full address
    setDisplayAddress(fullAddress.length > 20 ? formatAddress(fullAddress) : fullAddress); // Update the display address
  }

  const handleSubmitAddress = async () => {
    if (!walletAddress) {
      toast.info('Please input your wallet address.')
      return;
    }
    try {
      const gameData = await axios.post(`https://chronicles.casino/api/coinflip/submit`, { userhash, address: walletAddress });
      console.log('gameData.data', gameData.data)
      if (gameData.data.data) {
        toast.success("You have submited point of your streaks successfully.")
        setStreakCount(0)
        setTimeout(() => {
          setIsEndStreak(false)
        }, 100);
        setWalletAddress("")
        setDisplayAddress("")
        handleGetHash();
      }
    } catch (error) {
      console.log('error', error)
    }
  }

  const handlePlayAgain = () => {
    setIsLost(false);
    setImageFSource(fixedImage)
    handleGetHash();
  }

  const handleGetHash = () => {
    try {
      axios.get('https://chronicles.casino/api/coinflip').then(({ data }) => {
        setUserhash(data.data.userhash)
      })

    } catch (error) {
      console.log('error', error)
    }
  }

  const toggleMute = () => {
    if (audioRef.current) {
      audioRef.current.muted = !audioRef.current.muted;
    }
  };

  // Effect to play the sound whenever the imagevideoSource changes
  useEffect(() => {
    if (audioRef.current && result !== "") {
      audioRef.current.play(); // Play the sound
    }
  }, [result, statusChanged]);

  useEffect(() => {
    if (streakCount === 0 && attemptsCount && !isEndStreak) {
      setIsLost(true)
    }
  }, [streakCount])

  useEffect(() => {
    handleGetHash()
    const preloadImages = [hhImage, htImage, ttImage, fixedImage];
    preloadImages.forEach((image) => {
      const img = new Image();
      img.src = image;
    });
  }, [])

  return (
    <Box
      sx={{
        width: '100%',
        height: '100vh',
        backgroundImage: `url(${backgroundImage})`,
        backgroundSize: '100% 100%',
        display: 'flex',
        flexDirection: 'column',
        alignItems: 'center',
        color: 'white',
      }}
    >
      <Box
        display="flex"
        flexDirection="column"
        alignItems="center"
        p={2}
        bgcolor="#000000e8"
        style={{ height: '100vh', width: '100%', overflow: 'auto', position: 'relative' }}
      >
        <VolumeButton onToggleMute={toggleMute} />
        <Box
          component={'img'}
          src={logoImage}
          pt={3}
          sx={{
            width: { sm: '250px', xs: '200px' },
            cursor: 'pointer',
            transition: 'transform 0.2s', // Smooth transition for the scaling effect
            '&:hover': {
              transform: 'scale(1.05)', // Scale up slightly on hover
            },
          }}
          onClick={gotoHome}
        />
        <Box sx={{ width: { md: '80%', xs: '100%' } }} >
          <Typography variant="h6" sx={{ fontFamily: "'Press Start 2P' !important", fontSize: { md: 23, xs: 16 }, py: 3, textAlign: 'center' }}>
            AIRDROP 1: MULTIPLY YOUR $TERRY HOLDINGS
          </Typography>
          <Typography variant="h6" sx={{ fontFamily: "'Press Start 2P' !important", fontSize: { md: 23, xs: 16 } }}>
            {isLost ? `STREAK LOST` : `WIN STREAK: ${streakCount}`}
          </Typography>
        </Box>

        <Grid container spacing={{ md: 6, xs: 1 }} sx={{ width: { md: '90%', xs: '100%' } }}>
          <Grid item md={8} xs={12} sx={{ textAlign: 'center' }}>
            <Box sx={{ width: { sm: 600, xs: '100%' }, margin: 'auto', position: 'relative' }}>
              <Box sx={{ width: '100%', height: '100%', position: 'absolute', zIndex: 10 }} />
              <audio ref={audioRef}>
                <source src={sound} type="audio/ogg" />
                Your browser does not support the audio element.
              </audio>
              {!playingGif ?
                <img src={imageSource} className={`coin-image`} style={{ mixBlendMode: 'lighten', zIndex: -1, width: '100%' }} alt='' />
                :
                <img src={imageFSource} className={`coin-image`} style={{ mixBlendMode: 'lighten', zIndex: -1, width: '100%' }} alt='' />
              }

            </Box>

            {isLost ?
              <StyledButton
                variant="contained"
                sx={{
                  backgroundColor: '#34C9B1',
                  borderColor: 'white',
                  color: '#fff',
                  textAlign: 'center',
                  height: 60,
                  fontSize: { sm: '0.875rem !important', xs: '0.6rem !important' }
                }}
                onClick={handlePlayAgain}
              >
                PLAY AGAIN
              </StyledButton> :
              !isEndStreak ?
                <>
                  <Box display="flex" justifyContent="space-around" width="100%" marginBottom="20px" mb={2} gap={1}>
                    {buttonList.map((item, index) => (
                      <StyledButton
                        key={index}
                        index={index}
                        sx={activeId === item.id ? { bgcolor: '#34C9B1', color: 'white', fontSize: { sm: '0.875rem !important', xs: '0.550rem !important' } } : { fontSize: { sm: '0.875rem !important', xs: '0.550rem !important' } }}
                        onClick={() => handleSetActiveId(item.id)}
                      >
                        {item.label}
                      </StyledButton>
                    ))}
                  </Box>
                  <Box py={3}>
                    <StyledButton
                      variant="contained"
                      sx={{
                        marginRight: { sm: '30px', xs: '10px' },
                        backgroundColor: '#34C9B1',
                        borderColor: 'white',
                        color: '#fff',
                        textAlign: 'center',
                        height: { md: 60, xs: 'auto' },
                        fontSize: { sm: '0.875rem !important', xs: '0.6rem !important' }
                      }}
                      onClick={tossCoins}
                    >
                      TOSS
                    </StyledButton>
                    {streakCount > 1 && <StyledButton
                      variant="contained"
                      sx={{
                        backgroundColor: '#dc6068',
                        borderColor: 'white',
                        color: '#fff',
                        textAlign: 'center',
                        height: { md: 60, xs: 'auto' },
                        fontSize: { sm: '0.875rem !important', xs: '0.6rem !important' }
                      }}
                      onClick={() => setIsEndStreak(true)}
                    >
                      {"END STREAK"}
                    </StyledButton>}
                  </Box>
                </> :
                <>
                  <Box py={5}>
                    <TextField
                      variant="outlined"
                      placeholder='ENTER WALLET ADDRESS'
                      sx={{
                        marginRight: { sm: '20px', xs: '0' },
                        '& .MuiInputBase-root': {
                          height: 60,
                          bgcolor: '#34c9b1',
                          border: 'white solid',
                          borderRadius: '10px',
                          fontFamily: "'Press Start 2P'",
                          fontSize: { sm: '0.875rem !important', xs: '0.6rem !important' },
                          mb: 2
                        }
                      }}
                      value={displayAddress}
                      onChange={handleAddressChange}
                    >

                    </TextField>
                    <StyledButton
                      variant="contained"
                      sx={{
                        backgroundColor: '#34C9B1',
                        borderColor: 'white',
                        color: '#fff',
                        textAlign: 'center',
                        height: 60,
                        fontSize: { sm: '0.875rem !important', xs: '0.6rem !important' }
                      }}
                      onClick={handleSubmitAddress}
                    >
                      SUBMIT
                    </StyledButton>
                  </Box>
                </>
            }
          </Grid>
          <Grid item md={4} xs={12}>
            <Box mt={3} textAlign="left" width="100%" maxWidth="600px">
              {isLost ?
                <Typography style={{ fontFamily: 'Lato', fontSize: '18px', marginBottom: '20px', letterSpacing: 1 }}>
                  {'Good luck'}
                </Typography> :
                !isEndStreak ?
                  <>
                    <Typography style={{ fontFamily: 'Lato', fontSize: '18px', marginBottom: '20px', letterSpacing: 1 }}>
                      <Typography component={'span'} style={{ fontFamily: 'Lato', textDecoration: 'underline', letterSpacing: 2, fontSize: '18px', marginBottom: '10px' }}>
                        {'Rules:'}
                      </Typography>{'Try to score max win-streak submit your wallet. Aidrop will be calculated based on your maximum win streak. Minimum win-streak for submission: 2.'}
                    </Typography>
                    <br />
                    <Typography style={{ fontFamily: 'Lato', fontSize: '18px', marginBottom: '20px', letterSpacing: 1 }}>
                      <Typography component={'span'} style={{ fontFamily: 'Lato', textDecoration: 'underline', letterSpacing: 2, fontSize: '18px', marginBottom: '10px' }}>
                        {'Eg:'}
                      </Typography>{' Your max win streak is 7, you maybe aidropped xx% of the $TERRY that you hold. Exact calculation will be posted on our  X.'}
                    </Typography>
                    <br />
                    {streakCount > 1 && <Typography style={{ fontFamily: 'Lato', fontSize: '18px', marginBottom: '20px', letterSpacing: 1 }}>
                      <Typography component={'span'} style={{ fontFamily: 'Lato', textDecoration: 'underline', letterSpacing: 2, fontSize: '18px', marginBottom: '10px' }}>
                        {'End Streak'}
                      </Typography>{' will take you to wallet submission. Your win streak is recorded and ended.'}
                    </Typography>
                    }
                  </> :
                  <Typography style={{ fontFamily: 'Lato', fontSize: '18px', marginBottom: '20px', letterSpacing: 1 }}>
                    {'Make sure you enter and submit the same wallet that you hold your $TERRY in. You may do multiple submissions, but remember: only your maximum win streak is considered for aidrop calculation.'}
                  </Typography>
              }
            </Box>
          </Grid>
        </Grid>
        <SocialLinks />
      </Box>
    </Box >
  );
}

export default Play;
