import { Box } from "@mui/material"
import StyledIconButton from "./IconButton"
import LanguageIcon from '@mui/icons-material/Language';
import TelegramIcon from '@mui/icons-material/Telegram';
import CloseIcon from '@mui/icons-material/Close';

const SocialLinks = ({ position }) => {
    return (
        <Box
            sx={{
                position: { position },
                bottom: 40,
                display: 'flex',
                justifyContent: 'flex-end',
                pr: '10px',
                gap: 2,
                width: '95%'
            }}
        >
            <StyledIconButton color="primary" href="https://terry.money/" target="_blank" rel="noopener noreferrer">
                <LanguageIcon sx={{ color: 'white' }} />
            </StyledIconButton>
            <StyledIconButton color="primary" href="https://t.me/Terry_Ducktypus" target="_blank" rel="noopener noreferrer">
                <TelegramIcon sx={{ color: 'white' }} />
            </StyledIconButton>
            <StyledIconButton color="primary" href="https://x.com/terry_ducktypus" target="_blank" rel="noopener noreferrer">
                <CloseIcon sx={{ color: 'white' }} />
            </StyledIconButton>
        </Box>
    )
}

export default SocialLinks