import StyledIconButton from "./IconButton"
import VolumeUpIcon from '@mui/icons-material/VolumeUp';
import VolumeOffIcon from '@mui/icons-material/VolumeOff';
import { useState } from "react";

const VolumeButton = ({ onToggleMute }) => {
    const [volumeOff, setVolumeOff] = useState(false);

    const handleClick = () => {
        setVolumeOff(!volumeOff);
        onToggleMute(); // Call the function to toggle mute
    };


    return (
        <StyledIconButton
            color="primary"
            sx={{
                position: 'absolute',
                top: { sm: 40, xs: 30 },
                right: { sm: 40, xs: 30 },
            }}
            onClick={handleClick}

        >
            {volumeOff ? <VolumeOffIcon sx={{ color: 'white' }} /> : <VolumeUpIcon sx={{ color: 'white' }} />}
        </StyledIconButton>
    )
}

export default VolumeButton;